import { verifyPhone } from '../../../utils/utils.js'
import UserModel from '@/models/user.js'
import uniPopup from "@/components/uni-popup/uni-popup.vue"
const userModel = new UserModel() 
export default {
	data() {
		return {
			savePhone: true,
			surplusSecond: 0, // 还剩多少秒可以重发
			readySendCode: true,  // 是否准备好发送验证码
			verifyCodeTimer: null, //定时器对象
			focusArray: ['phone', 'verify'],
			focusIndex: null,  //焦点位置
			phone: '',
			verifyCode: '',
		}
	},
	beforeDestroy() {
		clearInterval(this.verifyCodeTimer)
		this.verifyCodeTimer = null
	},
	methods:{
		savePhoneChange(){
			this.savePhone = ! this.savePhone
		},
		handleBlur(index){
			let focusArray = this.focusArray
			if(index != focusArray.length - 1 ){
				if(!this[focusArray[index + 1]]){
					this.focusIndex = index + 1
					return
				}
				this.focusIndex = null
				return
			}
			this.focusIndex = null	
		},
		getVerifyCode(){
			if(!this.readySendCode){
				return 
			}
			if(!this.phone){
				uni.showToast({
					icon:'none',
					title:'请输入电话号码'
				})
				this.focusIndex = 0
				return
			}
			
			const correctPhone = verifyPhone(this.phone)
			if(!correctPhone){
				uni.showToast({
					icon:'none',
					title:'手机号码错误，请确认'
				})
				this.focusIndex = 0
				return
			} 
			uni.showLoading({
				mask: true
			})
			userModel.getVerifyCode(this.phone, 'A').then(res=>{
				// 请求成功,并判断code是否正确
				uni.hideLoading()
				const {code, message, data} = res.data
				if(code === '0'){
					uni.showToast({
						title: '验证码已发送'
					})
					this.verifyCodeStatus = 1
					this.readySendCode = false
					this.surplusSecond = 60
					this.focusIndex = 1
					this.verifyCodeTimer = setInterval(()=>{
						if(this.surplusSecond > 0){
							this.surplusSecond -= 1
						}else{
							this.surplusSecond = 0
							this.verifyCodeStatus = 0
							this.readySendCode = true
							clearInterval(this.verifyCodeTimer)
						}
					}, 1000)
					this.focusIndex = 1	
				}else{
					uni.hideLoading()
					uni.showToast({
						icon:'none',
						title:message
					})
				}
				
			}).catch(err=>{
				uni.showToast({
					icon:'none',
					title:'发送失败'
				})
			})
		},
		submit(){
			if(!this.phone){
				uni.showToast({
					icon:'none',
					title:'请输入电话号码'
				})
				this.$nextTick(()=>{
					this.focusIndex = 0
				})
				return
			}
			if(!this.verifyCode){
				uni.showToast({
					icon:'none',
					title:'请输入验证码'
				})
				this.$nextTick(()=>{
					this.focusIndex = 1
				})
				return
			}
			
			uni.showLoading({
				mask: true
			})
			const type = uni.getStorageSync('loginType')
			userModel.switchAccount(this.phone, this.verifyCode, type).then(res=>{
				uni.hideLoading()
				const { code, data, message } = res.data
				if(code === '0'){
					uni.showToast({
						title: '切换成功'
					})
					uni.setStorageSync('token', data.token)
					uni.setStorageSync('isLogin',data.isLogin)
					uni.setStorageSync('isRegister',data.isRegister)
					setTimeout(()=>{
						if(type == 'B'){
							uni.reLaunch({
								url: '../../seller/main/main'
							})	
						}else{
							uni.reLaunch({
								url: '../../customer/main/main'
							})
						}
						
					},2000)		
				}else{
					uni.showToast({
						icon: 'none',
						title: message
					})
				}
				
			})
			.catch(err=>{
				uni.hideLoading()
				uni.showToast({
					icon: 'none',
					title: '登录失败，请稍候再试'
				})
			})
		},
		// 去注册
		toRegister(){
			uni.navigateTo({
				url: '../register/register'
			})
		}
	}
}
